.title{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
    margin-top: 50px;
    min-height: 50px;
}
.title>*{
    margin: 0px;
}
.title>span{
    font-size: .9em;
}

table.major{
    margin: 50px auto;
    width:100%;
    max-width: 800px;
    font-size: 12px;
}

table.major thead td:nth-child(3){
    text-align: center;
}
table.major td{
    min-width: 50px;
}
/*Description*/
table.major td:nth-child(3){
    min-width: 200px;
}

.textRight{
    text-align: right;
}



